.Nav {
  z-index: 200;
  height: 3.4rem;
  width: 800px;
  margin: 0;
  padding: 0;
  padding-top: 1rem;
  color: inherit;
  background-color: transparent;
}

.Nav-items {
  margin: 0;
  padding: 0;
  width: 100%;
  list-style: none;

  display: flex;
  justify-content: space-between;
  align-items: center;  
}

.Nav-title {
  margin: 0;
  padding: 0;
  text-decoration: none;
  font-size: 2.4rem;
  font-weight: 400;
  /* letter-spacing: 2px; */
  color: inherit;
}

.Nav-icon {
  padding: 0;
  margin: 0;
  margin-left: 0.8rem;
  margin-right: 0.8rem;
  font-size: 1.6rem;
  color: inherit;
  cursor: pointer;
}


@media screen and (max-width: 1199px) {
  .Nav {
    width: 600px;
  }
}

@media screen and (max-width: 799px) {
  .Nav {
    width: 90%;
  }
}

@media screen and (max-width: 599px) {
  .Nav-title {
    font-size: 1.4rem;
  }
}
.Home {
  min-height: calc(100vh - 4.4rem);
  width: 100%;
  margin: 0;
  padding: 0;
  color: inherit;
  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Home-image {
  position: absolute;
  width: 100%;
  height: calc(100% - 4.4rem);
  background-color: var(--main-bg-color);
  background-image: url(/static/media/cpu.d6a695bb.png);
  background-repeat: no-repeat;
  background-size: 600px;
  background-position: 70% 40%;
  background-blend-mode: multiply;
}

.Home-content {
  z-index: 1;
  width: 600px;
}

.Home-fill {
  height: 0.2rem;
  width: 25%;
  margin-bottom: 0.5rem;
  background-color: var(--main-font-color);
}

.Home-text {
  width: 100%;
  margin: 0;
  margin-bottom: 1rem;
  padding: 0;
  font-size: var(--home-text-font-size);
  color: inherit;
}

.Home-social a {
  color: inherit;
}

.Home-social a:first-child {
  margin-right: 1rem;
}

.Home-icon {
  cursor: pointer;
  transition: color 0.4s ease;  
}

.Home-icon:hover {
  color: var(--active-font-color);
}

