.Music {
    color: var(--main-font-color);
    background-color: var(--nav-active-font-color);

    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.Music-timeline {
    height: 4px;
    background-color: var(--active-font-color);
}

.Music-controls {
    width: inherit;
    padding: 0.5rem;
    display: flex;
    align-items: center;
}

.Music-play {
    margin-right: 1rem;
}

.Music-control {
    margin: 0;
    padding: 0 0.5rem;
    cursor: pointer;
    color: inherit;
}

.Music-control:hover {
    color: var(--active-font-color);
    transition: color 0.4s ease;
}

.Music-slider {
    width: 4rem;
    -webkit-appearance: none;
    background: transparent; 
}

.Music-slider:focus {
    outline: none;
}

.Music-slider::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance: none;
    width: 1rem;
    height: 1rem;
    margin: 0;
    margin-top: -7px;
    padding: 0;
    border-radius: 50%;
    border: none;
    background: var(--main-font-color);
    cursor: pointer;
}

.Music-slider::-moz-range-thumb {
    width: 1rem;
    height: 1rem;
    border-radius: 50%;
    border: none;
    background: var(--main-font-color);
    cursor: pointer;
}

.Music-slider::-webkit-slider-runnable-track {
    width: 100%;
    height: 2px;
    background: var(--main-font-color);
}

.Menu a {
  text-decoration: none;
}

.Menu {
  height: 100%;
  width: 100%;
  padding-top: 2rem;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 300;
  background-color: var(--overlay-color);
  color: var(--nav-active-font-color);

  -webkit-transform: translateY(-200%);

          transform: translateY(-200%);
  transition: -webkit-transform 1s ease-in-out;
  transition: transform 1s ease-in-out;
  transition: transform 1s ease-in-out, -webkit-transform 1s ease-in-out;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.Menu-open {
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
}

.Menu-icon {
  cursor: pointer;
  transition: color 0.4s ease;  
}

.Menu-icon:hover {
  color: var(--active-font-color);
}

/* HEADER */
.Menu-header {
  max-width: 300px;
  padding-left: 4rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  -webkit-transform: translateY(-100%);

          transform: translateY(-100%);
  transition: -webkit-transform 1.5s ease-in-out;
  transition: transform 1.5s ease-in-out;
  transition: transform 1.5s ease-in-out, -webkit-transform 1.5s ease-in-out;
}

.Menu-name {
  display: block;
  margin-bottom: 1rem;
  font-size: 2.4rem;
  font-weight: 600;
  letter-spacing: 2px;
  color: inherit;
}

.Menu-avatar {
  margin-bottom: 3rem;
  height: 150px;
  width: auto;
  -webkit-filter: grayscale(0.2);
          filter: grayscale(0.2);
  border: 4px solid var(--nav-active-font-color);
}

.Menu-text {
  margin: 0;
  margin-bottom: 1rem;
  padding: 0;
  font-size: var(--main-font-size);
  color: inherit;
}

.Menu-social {
  margin-bottom: 3rem;
}

.Menu-social a {
  color: inherit;
}

.Menu-social a:first-child {
  margin-right: 1rem;
}

.Menu-open .Menu-header {
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
}
/* END HEADER */

/* NAV */
.Menu-items {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.Menu-item {
  display: block;
  position: relative;
  margin: 0;
  padding: 0;
  padding-right: 1rem;
  font-size: 4rem;
  letter-spacing: 8px;
  color: inherit;
}

.Menu-item:not(:last-child) {
  margin-bottom: 2rem;
}

.Menu-item:hover {
  box-shadow: 4px 0 0 0 var(--nav-active-font-color);
}

/* END NAV */

/* CLOSE */
.Menu-close {
  align-self: flex-start;
  padding-right: 4rem;
}
/* END CLOSE */

.menu-active {
  color: var(--active-font-color);
}

.animate-home {
  -webkit-animation-name: animateOpacity;
          animation-name: animateOpacity;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-duration: 1.2s;
          animation-duration: 1.2s;
}

.animate-about {
  -webkit-animation-name: animateOpacity;
          animation-name: animateOpacity;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-duration: 1.4s;
          animation-duration: 1.4s;
}

.animate-resume {
  -webkit-animation-name: animateOpacity;
          animation-name: animateOpacity;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-duration: 1.6s;
          animation-duration: 1.6s;
}

.animate-projects {
  -webkit-animation-name: animateOpacity;
          animation-name: animateOpacity;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-duration: 1.8s;
          animation-duration: 1.8s;
}

.animate-contact {
  -webkit-animation-name: animateOpacity;
          animation-name: animateOpacity;
  -webkit-animation-iteration-count: 1;
          animation-iteration-count: 1;
  -webkit-animation-timing-function: ease;
          animation-timing-function: ease;
  -webkit-animation-duration: 2.0s;
          animation-duration: 2.0s;
}

@-webkit-keyframes animateOpacity {
  0% { opacity:0; }
  80% { opacity:0; }
  100% { opacity:1; }
}

@keyframes animateOpacity {
  0% { opacity:0; }
  80% { opacity:0; }
  100% { opacity:1; }
}

.App {
  width: 100%;
  margin: 0;
  padding: 0;
  color: var(--main-font-color);
  background-color: var(--main-bg-color);

  display: flex;
  flex-direction: column;
  align-items: center;
}

.dark {
  --main-font-color: rgb(205, 205, 205);
  --nav-active-font-color: rgb(60, 60, 60);
  --active-font-color: rgb(72, 125, 175);
  --main-bg-color: rgb(80, 80, 80);
  --nav-hover-color: rgb(60, 60, 60);
  --shadow-color: rgb(40, 40, 40);
  --card-background-color: rgb(60, 60, 60);
  --overlay-color: rgb(205, 205, 205);
}

.light {
  --main-font-color: rgb(40,40,40);
  --nav-active-font-color: rgb(240, 240, 240);
  --active-font-color: rgb(63, 137, 205);
  --main-bg-color: rgb(235, 235, 235);
  --nav-hover-color: rgb(210, 210, 210);
  --shadow-color: rgb(190, 190, 190);
  --card-background-color: #FFF;
  --overlay-color: rgb(40, 40, 40);
}

/* CSS Vars */
:root {
  --main-font-color: rgb(40,40,40);
  --nav-active-font-color: rgb(240, 240, 240);
  --active-font-color: rgb(63, 137, 205);
  --nav-font-size: 1rem;
  --home-text-font-size: 1.6rem;
  --main-font-size: 1rem;
  --min-font-size: 0.9rem;
  --main-letter-space: 0.2rem;
  --main-bg-color: rgb(235, 235, 235);
  --nav-hover-color: rgb(210, 210, 210);
  --shadow-color: rgb(190, 190, 190);
  --card-background-color: #FFF;
}
/* END CSS Vars */

html {
  height: 100%;
  width: 100%;
}

body {
  overflow-y: scroll;
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Open Sans", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

